/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */
	a {
		//color: $color__link;
		color: white;

		&:visited {
			//color: $color__link-visited;
			color: white;
		}
		&:hover,
		&:focus,
		&:active {
			//color: $color__link-hover;
			color: white;
		}
		&:focus {
			outline: thin dotted;
		}
		&:hover,
		&:active {
			outline: 0;
		}
	}


/* Menus */

.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			float: left;
			position: absolute;
			top: $base__space-unit;
			left: -999rem;
			z-index: 99999;

			ul {
				left: -999rem;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}
	li {
		float: left;
		position: relative;

		&:hover > a,
		&.focus > a {
		}
	}
	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a {
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 768px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 $base__space-unit;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}

.navigation {
  height: 60px;
}

.banner {
  height: 100vh;
  background-size: cover, cover; 
  background-repeat: no-repeat, no-repeat;
  background-position: center, center;
}

.nav-li {
	padding: 0 10px;
}

.nav-green {
	a {
		color: $color__link;
	}
}

hr {
  border: 0.5px solid $color__background-hr;
}


.logo-white section:nth-child(2) {
	display: none;
}

.logo-green section:nth-child(1) {
	display: none;
}

.logo-wrapper * {
	height: 50px;
}

.logo-wrapper section {
	margin: 0;
}